header{position:sticky;top:0;width:100%;height:5em;padding:1em;background-color:var(--back);z-index:1000;display:flex;justify-content:space-between;align-items:center}header>div{display:flex;gap:3em}header a{text-decoration:none}header ul{list-style:none;display:flex;gap:1em;padding:0}header ul li{display:flex;align-items:center}header .big-header{display:block}header .small-header{display:none}header .small-header .header-h1{font-weight:450;margin:10px}@media(max-width: 1120px){header .big-header{display:none}header .small-header{display:block}}header .lang{background-color:rgba(0,0,0,0);color:var(--text)}header .lang:hover{scale:1}header .dropdown{position:absolute;box-shadow:0px 8px 16px 0px rgba(0,0,0,.2);top:40px;left:calc(50% - 75px);width:150px;height:auto;background-color:var(--menu);border-radius:10px;z-index:1000}header .dropdown .item{padding:12px 16px;color:var(--text);margin:0}header .dropdown .item:hover{background-color:var(--text-gray);color:var(--light);scale:1}header .dropdown .item.active{background-color:var(--secondary)}.list{list-style-type:none;background-color:var(--back);display:flex;flex-direction:column;justify-content:center;align-items:center;position:absolute;top:60px;left:0;width:100%;padding:50px}.icon:before{content:"ï††"}@media screen and (max-width: 1120px){.list{flex-direction:column;height:auto}.items:nth-child(1){border-top:1px solid var(--text-gray);margin-top:50px}.items{width:100%;border-top:1px solid var(--text-gray);text-align:center;margin-right:0px;padding:20px 0}}button,.button{cursor:pointer;border-radius:2em;padding:.5em 1em;border:none;outline:none;transition:all .3s ease}button.primary,.button.primary{background-color:var(--primary);color:var(--light)}button.secondary,.button.secondary{background-color:var(--secondary);color:var(--light)}button.outline,.button.outline{background-color:var(--light);color:var(--text);border:1px solid var(--text)}button:hover,.button:hover{scale:1.1}.sym-group{display:flex;flex-direction:column;justify-content:center;width:50px;height:60px;background-color:rgba(0,0,0,0)}.sym-group .btn-sym{width:30px;height:2px;background-color:var(--text);margin:3px 0}footer{width:100%;height:auto;background-color:#202030}footer .footer-icon{width:25px;height:20px}