.home {
    button, .button {
        cursor: pointer;
        border-radius: 2em;
        padding: 0.5em 1em;
        border: none;
        outline: none;
        transition: all 0.3s ease;

        &.primary {
            background-color: var(--primary);
            color: var(--light);
        }
        &.secondary {
            background-color: var(--secondary);
            color: var(--light);
        }

        &:hover {
            scale: 1.1;
        }
    }
    .skill-btn{
        background-color: var(--light);
        border: 0;
        padding: 10px 30px;
        color: var(--text);
        font-size: 18px;

        &:hover{
            animation-name: color-change;
            animation-duration: 0.3s;
            animation-fill-mode: forwards;
            scale: (1.02);
        }

        @keyframes color-change {
            from {background-color: var(--light);color: var(--text);}
            to {background-color: var(--secondary);color: var(--light);}
        }
    }
    .img-container{
        grid-area: img;
        height: min(35.9vw,517px);
        max-width: 792px;
    
        .main-img{
            height: 100%;
            width: auto;
        }  
    }
    .select-part {
        margin: 10px;
        padding: 20px;
        position: relative;
        &:before{
            position: absolute;
            height: 100%;
            width: 6px;
            top: 0;
            left: 0;
            background-color: var(--light);
            content: "";
            border-radius: 40px;
            opacity: 0.5;
        }
    }
    .blog-container{
        width: 100%;
        height: 100%;
        max-height: 200px;
        overflow: hidden;
    }
    .blog-img{
        width: 100%;
        height: 100%;
        border-radius: 10px;
        object-fit: cover;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover {
            scale: (1.1);
        }
    }
    .card {
        background-color: var(--back-card);
        color: var(--text);

        &:hover {
            animation-name: back-change;
            animation-duration: 0.3s;
            animation-fill-mode: forwards;
        }

        @keyframes back-change {
            from {background-color: var(--back-card);color: var(--text)}
            to {background-color: var(--primary);color: var(--light);}
        }
    }
    .separate {
        width: 100%;
        height: 1px;
        background-color: var(--text-gray);
        margin-top: 50px;
        margin-bottom: 50px;
    }
    .sym-area {
        height: 50px;
        width: 50px;
        position: relative;
        cursor: pointer;
        .v {
            height: 30px;
            width: 2px;
            background-color: var(--text);
            position: absolute;
            top: calc(50% - 13px);
            left: 50%;
        }
        .h {
            height: 2px;
            width: 30px;
            background-color: var(--text);
            position: absolute;
            top: 50%;
            left: calc(50% - 14px);
        }
    }
}
a{
    text-decoration: none;
}
