:root {

	--primary:		#4737ff;
	--secondary:	#f75c03;

	--success:		#00c9a7;
	--danger:		#de4437;
    --danger-bg:	#ffefff;

	--text:			#202030;
	--text-gray:	#5e5e6a;

	--back:			#f8f8f9;
	--back-card:	#f0efff;
	--light:		white;

	--menu:         #f1f1f1;

	--night-primary:#527cf1;
	--night-secondary:#f3610c;

	--night-success:#00c9a7;
	--night-danger:	#de4437;
    --night-danger-bg:#ffefff;

	--night-text:	#f0efff;
	--night-text-gray:	#d1d1d1;

	--night-back:	#141414;
	--night-back-card:	#202020;
	--night-light:	rgb(0, 0, 0);

	--night-menu:  #202020;

	--xxl:			1300px;
	--xl:			1120px;
	--lg:			960px;
	--md:			756px;
	--sm:			576px;

	--normal:		"Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	--monospace:	SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

.night {
	--primary:		var(--night-primary);
	--secondary:	var(--night-secondary);

	--success:		var(--night-success);
	--danger:		var(--night-danger);
    --danger-bg:	var(--night-danger-bg);

	--text:			var(--night-text);
	--text-gray:	var(--night-text-gray);

	--back:			var(--night-back);
	--back-card:	var(--night-back-card);
	--light:		var(--night-light);

	--menu:         var(--night-menu);
}

* {box-sizing: border-box;}

.scroll {
	overflow-y: auto;

	&::-webkit-scrollbar {
		position: absolute;
		width: 5px;
		height: 5px;
	}

	&::-webkit-scrollbar-track {
		background: #ddd;
	}

	&::-webkit-scrollbar-thumb {
		background: #aaa;
		border-radius: 5px;
	}

	&::-webkit-scrollbar-thumb:hover {
		background: #888;
	}
}
body {
	padding: 0;
	margin: 0;
    font-family: var(--normal);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
    font-size: 16px;
}

.main {
	min-height: 100%;
	color: var(--text);
	background-color: var(--back);
}

input, select, textarea, button {
	font-size: 1em;
	font-family: var(--normal);
}

.container {
	display: block;
	max-width: var(--xxl);
	margin: auto;
	padding: 0 20px;

	@media (max-width: 1200px) {
		padding: 0 40px;
	}

	@media (max-width: 575px) {
		padding: 0 10px;
	}
}

section {
	width: 100%;
}

a {
	color: var(--text);
	&:hover {
		color: var(--text-gray);
	}
}

h1, h2, h3, h4 {
	margin: 0;
}

h1, .h1 {
	font-size: clamp(34px,4.03vw,58px);
	font-weight: bold;
	// font-family: var(--bold);
}
h2, .h2 {
	font-size: clamp(28px,2.92vw,42px);
	font-weight: bold;
}

h3, .h3 {
	font-size: clamp(24px,2.36vw,34px);
	font-weight: bold;
}

h4, .h4 {
	font-size: clamp(20px,1.67vw,24px);
	font-weight: bold;
}

.d {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	flex-wrap: wrap;
	&.column {
		flex-direction: column;
	}
	&.nowrap {
		flex-wrap: nowrap;
	}

	&.justify {
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between;
	}
	&.center {
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
	}
	&.middle {
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
	}
	&.gap-sm {
		gap: 0.5em;
	}
	&.gap {
		gap: 1em;
	}
}

.d-row{
	display: flex;
	flex-direction: row;
}
.d-column{
	display: flex;
	flex-direction: column;
}

.col1 {
	flex: 0 0 auto;
	width: 8.33%;
}
.col2 {
	flex: 0 0 auto;
	width: 16.66%
}
.col3 {
	flex: 0 0 auto;
	width: 25%
}
.col4 {
	flex: 0 0 auto;
	width: 33.33%
}
.col5 {
	flex: 0 0 auto;
	width: 41.66%
}
.col6 {
	flex: 0 0 auto;
	width: 50%
}
.col7 {
	flex: 0 0 auto;
	width: 58.33%
}
.col8 {
	flex: 0 0 auto;
	width: 66.66%
}
.col9 {
	flex: 0 0 auto;
	width: 75%
}
.col10 {
	flex: 0 0 auto;
	width: 83.33%
}

.col11 {
	flex: 0 0 auto;
	width: 91.66%
}

.col12 {
	flex: 0 0 auto;
	width: 100%
}


.row {
	margin-left: -0.5em;
	margin-right: -0.5em;
}
.col {
	padding-left: 0.5em;
	padding-right: 0.5em;
}

@media (max-width: 1300px) {
	.col1-lg, .col2-lg, .col3-lg, .col4-lg, .col5-lg, .col6-lg, .col7-lg, .col8-lg, .col9-lg, .col10-lg, .col11-lg, .col12-lg {
		flex: 0 0 50%
	}
}
@media (max-width: 960px) {
	.col1-md, .col2-md, .col3-md, .col4-md, .col5-md, .col6-md, .col7-md, .col8-md, .col9-md, .col10-md, .col11-md, .col12-md {
		flex: 0 0 100%
	}
}

@media (max-width: 576px) {
	.col1-sm, .col2-sm, .col3-sm, .col4-sm, .col5-sm, .col6-sm, .col7-sm, .col8-sm, .col9-sm, .col10-sm, .col11-sm, .col12-sm {
		flex: 0 0 100%
	}
}

